import cx from "classnames";
import React from "react";

import { Props as ContentProps } from "../types";
import { Cell as CellType } from "./util";

type Props = Readonly<{
  cell: CellType;
  onSelectBlock: ContentProps["onSelectBlock"];
  selectedCell: string | null;
}>;

export const Cell = ({ cell, onSelectBlock, selectedCell }: Props) => {
  return (
    <td
      className={cx({ selected: !!selectedCell && selectedCell === cell.id })}
      colSpan={cell.colSpan}
      id={`support-${cell.id}`}
      onClick={e => {
        if (!cell.id || !onSelectBlock) {
          return;
        }
        e.stopPropagation();
        onSelectBlock(cell.id, cell.text);
      }}
      rowSpan={cell.rowSpan}
    >
      {cell.contents}
    </td>
  );
};
Cell.displayName = "Cell";
