import cx from "classnames";
import React from "react";
import { Button, Col, Form, Row, Spinner } from "reactstrap";

import { IActionMap } from "../state/actions";
import { IAnnotation, IAnnotations, ITaxonomies, Taxonomy } from "../types";
import { AnnotationsList } from "./AnnotationsList";
import { Header } from "./Header";

interface IProps {
  readonly actions: IActionMap;
  readonly annotations: IAnnotations;
  readonly loading: boolean;
  readonly maximumTermLength: number;
  readonly onSave: () => void;
  readonly review: boolean;
  readonly saving: boolean;
  readonly taxonomies: ITaxonomies;
}

const EMPTY_ANNOTATION_LIST: ReadonlyArray<IAnnotation> = [];

export const Annotations = ({
  actions,
  annotations,
  loading,
  maximumTermLength,
  onSave,
  review,
  saving,
  taxonomies,
}: IProps) => {
  if (loading) {
    return null;
  }
  const taxonomyKeys = Object.keys(taxonomies);
  const grid: Array<Array<Taxonomy>> = [];
  let currentRow = [];
  let itemsPerRow = 3;
  if (taxonomyKeys.length > 3 && taxonomyKeys.length % 2 === 0) {
    itemsPerRow = 2;
  }
  for (const taxonomyKey of taxonomyKeys) {
    currentRow.push(taxonomies[taxonomyKey]);
    if (currentRow.length >= itemsPerRow) {
      grid.push(currentRow);
      currentRow = [];
    }
  }
  if (currentRow.length > 0) {
    grid.push(currentRow);
  }
  return (
    <Form
      className="annotations-wrapper"
      onSubmit={e => {
        e.preventDefault();
        onSave();
      }}
    >
      <div>
        {grid.map((row, index) => (
          <Row className="annotations-row" key={`rows-${index}`}>
            {row.map((t, i) => {
              let largeSize = 4;
              let midSize = 6;
              const className = {
                "offset-lg-2": row.length === 1,
              };
              if (row.length === 1) {
                largeSize = 8;
                midSize = 12;
              } else if (row.length === 2) {
                largeSize = 6;
              }
              const annotationList =
                t.id in annotations ? annotations[t.id] : EMPTY_ANNOTATION_LIST;
              return (
                <Col
                  className={cx(className, "mb-4 mb-md-0")}
                  key={t.id}
                  md={midSize}
                  lg={largeSize}
                  xs={12}
                >
                  <Header count={annotationList.length} title={t.name} />
                  <AnnotationsList
                    actions={actions}
                    annotations={annotationList}
                    inputOrder={i + 1}
                    maximumTermLength={maximumTermLength}
                    review={review}
                    taxonomy={t}
                  />
                </Col>
              );
            })}
          </Row>
        ))}
      </div>
      {!review && (
        <Row>
          <Col
            className="text-right"
            md={12}
            lg={{
              offset: taxonomyKeys.length === 1 ? 2 : 0,
              size: taxonomyKeys.length === 1 ? 8 : 12,
            }}
            xs={12}
          >
            <Button className="mt-3 save-button" color="primary" disabled={saving} type="submit">
              {saving ? "Saving" : "Save"}
              {saving && <Spinner className="ml-2" />}
            </Button>
          </Col>
        </Row>
      )}
    </Form>
  );
};
Annotations.displayName = "Annotations";
