import React from "react";

import { ContentLoader } from "@common/components/util/ContentLoader";
import { Loading } from "@common/components/util/Loading";
import { Workbench } from "@common/components/Workbench";
import * as Constants from "@indexing/constants";
import { IndexingWrapper } from "./IndexingWrapper";

type Props = Readonly<{
  review: boolean;
}>;

const loadRoles = [
  "indexing-predicted-xml",
  "indexing-reviewed-xml",
  "indexing-taxonomy-json",
  "zoning-pagehr-png",
];

export const IndexingWorkbench = ({ review }: Props) => {
  return (
    <Workbench
      blockingStep={Constants.TASK_KIND}
      loadRoles={loadRoles}
      review={review}
      settings={<></>}
      workbenchTitle={Constants.APP_TITLE}
    >
      {({ actions: { checkinJob, saveContent }, auth, contents, job }) => {
        const contentToLoad = contents.filter(
          c =>
            c.role === "indexing-predicted-xml" ||
            c.role === "indexing-reviewed-xml" ||
            c.role === "indexing-taxonomy-json",
        );
        const saveJob = async (xml: string) => {
          const role = "indexing-reviewed-xml";
          let order = 1;
          let isNew = true;
          const reviewedContent = contents.find(c => c.role === "indexing-reviewed-xml");
          if (reviewedContent) {
            order = reviewedContent.order;
            isNew = false;
          }
          await saveContent(role, order, xml, true, isNew, "application/xml", undefined, true);
          await checkinJob();
        };
        return (
          <ContentLoader
            allowFailures={["indexing-reviewed-xml"]}
            auth={auth.authString}
            content={contentToLoad}
          >
            {results => {
              if (results.ready) {
                return (
                  <div className="h-100">
                    <IndexingWrapper
                      contents={contents}
                      jobId={job.id}
                      jobMetadata={job.metadata}
                      jobName={job.name}
                      results={results.contents}
                      review={review}
                      saveJob={saveJob}
                      token={auth.token}
                    />
                  </div>
                );
              }
              return <Loading progress={results.progress} text={"Loading job details"} />;
            }}
          </ContentLoader>
        );
      }}
    </Workbench>
  );
};
IndexingWorkbench.displayName = "IndexingWorkbench";
