import React, { useCallback, useMemo, useState } from "react";

import { ApiJob, ApiJobContent } from "@common/api/v1.1/types/jobs";
import { ResolvedContent } from "@common/components/util/ContentLoader";
import { Indexing } from "@indexing/components/Indexing";
import { State } from "@indexing/components/Indexing/state";
import { IIndexingDocument, IPredictions, ITaxonomies } from "@indexing/components/Indexing/types";
import { buildDocumentAndPredictions, insertDataPoints } from "./innodom";

type Props = Readonly<{
  contents: ReadonlyArray<ApiJobContent>;
  jobId: ApiJob["id"];
  jobMetadata: ApiJob["metadata"];
  jobName: ApiJob["name"];
  results: ReadonlyArray<ResolvedContent>;
  review: boolean;
  saveJob: (xml: string) => Promise<void>;
  token: string;
}>;

export const IndexingWrapper = ({
  contents,
  jobId,
  jobMetadata,
  jobName,
  saveJob,
  results,
  review,
  token,
}: Props) => {
  const [saving, setSaving] = useState(false);
  const taxonomies: ITaxonomies = useMemo(() => {
    return results
      .filter(c => c.role === "indexing-taxonomy-json")
      .reduce((prev: ITaxonomies, next: ResolvedContent): ITaxonomies => {
        if (!next.contents) {
          return prev;
        }
        let parsed = next.contents;
        if (typeof parsed === "string") {
          parsed = JSON.parse(parsed);
        }
        prev[parsed.id] = {
          id: parsed.id,
          name: parsed.name,
          open: true,
        };
        return prev;
      }, {});
  }, [results]);
  const xml: string = useMemo(() => {
    const reviewedContent = results.find(
      c => c.role === "indexing-reviewed-xml" && typeof c.contents === "string",
    );
    if (reviewedContent) {
      return reviewedContent.contents;
    }
    const predictedContent = results.find(
      c => c.role === "indexing-predicted-xml" && typeof c.contents === "string",
    );
    if (predictedContent) {
      return predictedContent.contents;
    }
    throw new Error("Unable to fetch XML content.");
  }, [results]);
  const docBuildResult = useMemo((): {
    document: IIndexingDocument;
    predictions: IPredictions;
    xmlDocument: Document;
  } => {
    const { document, predictions, xmlDocument } = buildDocumentAndPredictions(
      xml,
      contents,
      jobName,
      token,
      jobMetadata,
    );
    if (document !== null && predictions !== null && xmlDocument !== null) {
      return { document, predictions, xmlDocument };
    }
    throw new Error("Unable to find document or predictions.");
  }, [contents, jobMetadata, jobName, token, xml]);
  const onSave = useCallback(
    async (annotations: State["annotations"]) => {
      if (review) {
        return;
      }
      setSaving(true);
      try {
        await saveJob(insertDataPoints(annotations, docBuildResult.xmlDocument));
      } catch (err) {
        setSaving(false);
      }
    },
    [docBuildResult.xmlDocument, review, saveJob, setSaving],
  );
  return (
    <Indexing
      document={docBuildResult.document}
      jobId={jobId}
      onSave={onSave}
      predictions={docBuildResult.predictions}
      review={review}
      saving={saving}
      taxonomies={taxonomies}
    />
  );
};
IndexingWrapper.displayName = "IndexingWrapper";
