import cx from "classnames";
import React from "react";

import { Props as ContentProps } from "../types";
import { Cell } from "./util";

type Props = Readonly<{
  cell: Cell;
  onSelectBlock: ContentProps["onSelectBlock"];
  selectedCell: string | null;
}>;

export const Header = ({ cell, onSelectBlock, selectedCell }: Props) => {
  return (
    <th
      className={cx({ selected: !!selectedCell && selectedCell === cell.id })}
      colSpan={cell.colSpan}
      onClick={() => {
        if (!cell.id || !onSelectBlock) {
          return;
        }
        onSelectBlock(cell.id, cell.text);
      }}
      rowSpan={cell.rowSpan}
    >
      {cell.contents}
    </th>
  );
};
Header.displayName = "Header";
