import React, { memo } from "react";

import { TAG_IMAGE, TAG_MATH, TAG_TABLE, TAG_TEXT } from "./constants";
import { InnodomImage as Image } from "./InnodomImage";
import { Table } from "./Table";
import { Text } from "./Text";
import { Props } from "./types";

export const InnodomContent = memo((props: Props) => {
  switch (props.el.nodeName) {
    case TAG_TABLE:
      return <Table {...props} />;
    case TAG_TEXT:
      return <Text {...props} selectedCell={null} />;
    case TAG_MATH:
    case TAG_IMAGE:
      return <Image {...props} selectedCell={null} />;
    default:
      console.warn("Unknown block:", props.el.nodeName);
      return null;
  }
});
InnodomContent.displayName = "InnodomContent";
