import cx from "classnames";
import React, { useState } from "react";

import { ATTR_ID } from "../constants";
import { Props } from "../types";
import { Row } from "./Row";
import { extractTableInformation } from "./util";

export const Table = ({ el, fontInformation, onSelectBlock, selected, selectedCell }: Props) => {
  const tableId = el.getAttribute(ATTR_ID) || "";
  const [mouseWithin, setMouseWithin] = useState(false);
  const { headers, rows } = extractTableInformation(el, fontInformation);
  return (
    <div
      className={cx("block", "p-3", { selected, within: mouseWithin })}
      onClick={() => {
        if (mouseWithin || !onSelectBlock) {
          return;
        }
        onSelectBlock(tableId, "");
      }}
    >
      <table
        className="table table-bordered table-sm table-striped"
        id={`support-${tableId}`}
        onMouseEnter={() => {
          setMouseWithin(true);
        }}
        onMouseLeave={() => {
          setMouseWithin(false);
        }}
      >
        <thead>
          {headers.map((r, rowIndex) => (
            <Row
              key={`row-${rowIndex}`}
              isHeader
              onSelectBlock={onSelectBlock}
              selectedCell={selected ? null : selectedCell}
              row={r}
            />
          ))}
        </thead>
        <tbody>
          {rows.map((r, rowIndex) => (
            <Row
              key={`row-${rowIndex}`}
              isHeader={false}
              onSelectBlock={onSelectBlock}
              selectedCell={selected ? null : selectedCell}
              row={r}
            />
          ))}
        </tbody>
      </table>
    </div>
  );
};
Table.displayName = "Table";
