import React from "react";

import { Props as ContentProps } from "../types";
import { Cell } from "./Cell";
import { Header } from "./Header";
import { Row as RowType } from "./util";

type Props = Readonly<{
  isHeader: boolean;
  row: RowType;
  onSelectBlock: ContentProps["onSelectBlock"];
  selectedCell: string | null;
}>;

export const Row = ({ isHeader, onSelectBlock, selectedCell, row }: Props) => (
  <tr>
    {row.cells.map((cell, cellIndex) =>
      isHeader ? (
        <Header
          cell={cell}
          key={`cell-${cellIndex}`}
          onSelectBlock={onSelectBlock}
          selectedCell={selectedCell}
        />
      ) : (
        <Cell
          cell={cell}
          key={`cell-${cellIndex}`}
          onSelectBlock={onSelectBlock}
          selectedCell={selectedCell}
        />
      ),
    )}
  </tr>
);
Row.displayName = "Row";
