import cx from "classnames";
import React from "react";

import { TAG_BLOCK } from "../constants";
import { Props } from "../types";
import { extractTextBlock } from "./util";

export const Text = ({ el, fontInformation, onSelectBlock, selected }: Props) => {
  const textId = el.getAttribute("id") || "";
  const blocks = Array.from(el.getElementsByTagName(TAG_BLOCK));
  return (
    <p
      className={cx("block", { selected })}
      id={`support-${textId}`}
      onClick={() => {
        if (!textId || !onSelectBlock) {
          return;
        }
        onSelectBlock(textId, el.textContent || "");
      }}
    >
      {blocks.map((b, index) => extractTextBlock(b, `${textId}-block-${index}-`, fontInformation))}
    </p>
  );
};
Text.displayName = "Text";
