import React from "react";
import { ButtonGroup } from "reactstrap";

interface IProps {
  readonly html: string | undefined;
  readonly pdf: string | undefined;
  readonly xml: string | undefined;
}

const isDocumentSource = (key: string): key is keyof IProps => {
  switch (key) {
    case "html":
    case "pdf":
    case "xml":
      return true;
    default:
      return false;
  }
};

export const DocumentSources = (props: IProps) => {
  const nonEmptyKeys: Array<keyof IProps> = Object.keys(props).reduce(
    (prev, next: string) => (isDocumentSource(next) && props[next] ? prev.concat([next]) : prev),
    [] as Array<keyof IProps>,
  );
  if (nonEmptyKeys.length < 1) {
    return null;
  }
  return (
    <ButtonGroup className="document-sources">
      {nonEmptyKeys.map((source: keyof IProps, _index: number) => (
        <a className="btn btn-outline-primary" key={source} href={props[source]} target="_blank">
          View {source.toLocaleUpperCase()}
        </a>
      ))}
    </ButtonGroup>
  );
};
DocumentSources.displayName = "DocumentSources";
